var projection = getProjection("EPSG:3857");
var projectionExtent = projection.getExtent();
var size = getWidth(projectionExtent) / 256;

var pyramidCountAbitibi = 23;
var resolutionsAbitibi = new Array(pyramidCountAbitibi);
var matrixIdsAbitibi = new Array(pyramidCountAbitibi);
for (var z = 0; z < pyramidCountAbitibi; ++z) {
  // generate resolutions and matrixIds arrays for this WMTS
  resolutionsAbitibi[z] = size / Math.pow(2, z);
  matrixIdsAbitibi[z] = z;
}

var pyramidCountOntario = 18;
var resolutionsOntario = new Array(pyramidCountOntario);
var matrixIdsOntario = new Array(pyramidCountOntario);
for (var y = 0; y < pyramidCountOntario; ++y) {
  // generate resolutions and matrixIds arrays for this WMTS
  resolutionsOntario[y] = size / Math.pow(2, y);
  matrixIdsOntario[y] = y;
}

var pyramidCountBase = 18;
var resolutionsBase = new Array(pyramidCountBase);
var matrixIdsBase = new Array(pyramidCountBase);
for (var y = 0; y < pyramidCountBase; ++y) {
  // generate resolutions and matrixIds arrays for this WMTS
  resolutionsBase[y] = size / Math.pow(2, y);
  matrixIdsBase[y] = y;
}

var view = new View({
  center: [-8823588, 6013538],
  zoom: 8
});

var zip = new JSZip();

function getKMLData(buffer) {
  var kmlData;
  zip.load(buffer);
  var kmlFile = zip.file(/.kml$/i)[0];
  if (kmlFile) {
    kmlData = kmlFile.asText();
  }
  return kmlData;
}

function getKMLImage(href) {
  var url = href;
  var path = window.location.href;
  path = path.slice(0, path.lastIndexOf("/") + 1);
  if (href.indexOf(path) === 0) {
    var regexp = new RegExp(href.replace(path, "") + "$", "i");
    var kmlFile = zip.file(regexp)[0];
    if (kmlFile) {
      url = URL.createObjectURL(new Blob([kmlFile.asArrayBuffer()]));
    }
  }
  return url;
}

var KMZ = /*@__PURE__*/ (function (KML) {
  function KMZ(opt_options) {
    var options = opt_options || {};
    options.iconUrlFunction = getKMLImage;
    KML.call(this, options);
  }

  if (KML) KMZ.__proto__ = KML;
  KMZ.prototype = Object.create(KML && KML.prototype);
  KMZ.prototype.constructor = KMZ;

  KMZ.prototype.getType = function getType() {
    return "arraybuffer";
  };

  KMZ.prototype.readFeature = function readFeature(source, options) {
    var kmlData = getKMLData(source);
    return KML.prototype.readFeature.call(this, kmlData, options);
  };

  KMZ.prototype.readFeatures = function readFeatures(source, options) {
    var kmlData = getKMLData(source);
    return KML.prototype.readFeatures.call(this, kmlData, options);
  };

  return KMZ;
})(KML);

function getOffsetX(name) {
  if (name === "Upper-Beaver") {
    return -30;
  }
  if (name === "Granada") {
    return 13;
  } else {
    return 0;
  }
}

function getOffsetY(name) {
  if (name === "Upper-Beaver") {
    return -20;
  }
  if (name === "Granada") {
    return 5;
  } else {
    return -15;
  }
}

var projectsLabel = function (feature) {
  return new Text({
    textAlign: "left",
    text: feature.get("Name"),
    font: "bold 18px Arial, sans-serif",
    fill: new Fill({ color: "rgba(0, 0, 0, 1)" }),
    stroke: new Stroke({ color: "rgba(255, 255,255, 1)", width: 3 }),
    offsetX: getOffsetX(feature.get("Name")),
    offsetY: getOffsetY(feature.get("Name"))
  });
};

function pointStyleFunction(feature, resolution) {
  return new Style({
    image: new CircleStyle({
      radius: 8,
      fill: new Fill({ color: "rgba(0, 255, 0, 1)" }),
      stroke: new Stroke({ color: "black", width: 1 })
    }),
    text: projectsLabel(feature)
  });
}

var clipVectorSource = new VectorSource({
  url: at,
  projection: "EPSG:4326",
  format: new GeoJSON()
});

var clipLayer = new VectorLayer({
  source: clipVectorSource,
  displayInLayerSwitcher: false
});

var ontarioAerialPhotoLayer = new TileLayer({
  minZoom: 13,
  opacity: 0.5,
  title: "Aerial photography",
  source: new WMTS({
    url:
      "https://services.arcgisonline.com/arcgis/rest/services/World_Imagery/MapServer/WMTS/",
    layer: "World_Imagery",
    matrixSet: "default028mm",
    format: "image/png",
    projection: projection,
    tileGrid: new WMTSTileGrid({
      origin: getTopLeft(projectionExtent),
      resolutions: resolutionsOntario,
      matrixIds: matrixIdsOntario
    }),
    style: "default",
    wrapX: true
  }),
  displayInLayerSwitcher: false
});

