aerialPhotoLayer.on("change:opacity", function (evt) {
  ontarioAerialPhotoLayer.setOpacity(aerialPhotoLayer.getOpacity());
  console.log(ontarioAerialPhotoLayer);
});

aerialPhotoLayer.on("change:visible", function (evt) {
  ontarioAerialPhotoLayer.setVisible(aerialPhotoLayer.getVisible());
});

clipVectorSource.on("change", function (evt) {
  var source = evt.target;
  if (source.getState() === "ready") {
    var clipFeature = clipVectorSource.getFeatures()[0];
    var cropQuebec = new Crop({ feature: clipFeature, inner: false });
    var cropOntario = new Crop({ feature: clipFeature, inner: true });
    aerialPhotoLayer.addFilter(cropQuebec);
    ontarioAerialPhotoLayer.addFilter(cropOntario);
  }
});

var basemapLayer = new TileLayer({
  displayInLayerSwitcher: false,
  source: new WMTS({
    url:
      "https://server.arcgisonline.com/arcgis/rest/services/World_Street_Map/MapServer/WMTS/1.0.0",
    layer: "World_Street_Map",
    matrixSet: "default028mm",
    format: "image/jpeg",
    projection: projection,
    tileGrid: new WMTSTileGrid({
      origin: getTopLeft(projectionExtent),
      resolutions: resolutionsBase,
      matrixIds: matrixIdsBase
    }),
    style: "default",
    wrapX: true
  })
});

